
import store from "@/store";
import axios from "axios";
import { defineComponent, onMounted, onUnmounted, reactive } from "vue";
import Chart from "./chart/index";
export default defineComponent({
  components: {
    Chart,
  },
  setup() {
    // 下层数据
    const dataArr = [];
    // 对应图标
    const iconFont = [
      "icon-diagnose",
      "icon-monitoring",
      "icon-cloudupload",
      "icon-clouddownload",
      "icon-clouddownload",
    ];
    const numberData = reactive([]);
    let intervalInstance = null;
    onMounted(() => {
      getData();
      // changeTiming()
    });

    const setData = () => {
      dataArr.forEach((e) => {
        numberData.push({
          config: {
            number: [e.number],
            // toFixed: 1,
            content: "{nt}",
            style: {
              fontSize: 24,
            },
          },
          text: e.text,
        });
      });
    };

    const getData = () => {
      let res = store.getters.data;
      for (let i = 0; i < 4; i++) {
        let e = res.job.countByType[i];
        dataArr.push({
          number: e.count,
          text: e.name,
        });
      }
      setData();
    };

    const changeTiming = () => {
      intervalInstance = setInterval(() => {
        changeNumber();
      }, 2000);
    };
    const changeNumber = () => {
      numberData.forEach((item, index) => {
        item.config.number[0] += ++index;
        item.config = { ...item.config };
      });
    };
    onUnmounted(() => {
      clearInterval(intervalInstance);
    });

    return { numberData, iconFont };
  },
});
